/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Smooth Scroll */
html {
  scroll-behavior: smooth;
}

/* Base Styles */
body {
  height: 100%;
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: white;
  overflow-x: hidden;
}

/* Container for all sections */
.home-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem 1rem;
}

/* Section layout */
section {
  padding: 4rem 2rem;
  min-height: 100vh;
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 3rem 1rem;
}

.hero h1 {
  font-size: 2.5rem;
}

.hero p {
  font-size: 1.2rem;
  margin: 1rem 0;
}

.hero-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #ffffff20;
  color: white;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.hero-button:hover {
  background-color: #ffffff40;
}

.about-section {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  backdrop-filter: blur(5px);
  margin-top: 20px;
}

.about-section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 1rem;
  text-align: center;
  letter-spacing: 2px;
}

.about-section p {
  font-size: 1.1rem;
  color: #ffffff;
  text-align: justify;
  margin-bottom: 1rem;
  max-width: 100%;
}

/* Adjust for smaller screens */
@media (max-width: 768px) {
  .about-section {
    padding: 15px;
  }

  .about-section h2 {
    font-size: 2rem;
  }

  .about-section p {
    font-size: 1rem;
  }
}

/* About, Projects, Certifications, Contact */
.project-section,
.certification-section,
.contact-section {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  border-radius: 10px;
  backdrop-filter: blur(3px);
}

/* Reusable link-style button */
.link-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #ffffff20;
  color: white;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 6px;
  transition: 0.3s;
}

.link-button:hover {
  background-color: #ffffff40;
}

/* Project Cards */
.project-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.card {
  flex: 1 1 45%;
  background-color: rgba(255, 255, 255, 0.12);
  padding: 16px;
  border-radius: 10px;
  backdrop-filter: blur(5px);
  position: relative;
  z-index: 0;
  overflow: hidden;
  transition: box-shadow 0.3s ease, border 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.card::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  border-radius: 10px;
  background: linear-gradient(
          120deg,
          rgba(0, 255, 255, 0.3),
          rgba(255, 255, 255, 0.1),
          rgba(255, 0, 200, 0.3)
  );
  background-size: 300% 300%;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease, background-position 1.5s ease;
  animation: subtle-shimmer 5s linear infinite;
  pointer-events: none;
}

.card:hover::before {
  opacity: 0.6;
  background-position: 100% 0%;
}

@keyframes subtle-shimmer {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 200% 0%;
  }
}



.cert-cards {
   display: flex;
   flex-wrap: wrap;
   gap: 16px;
 }

.cert-cards .card {
  flex: 1 1 45%;
  background-color: rgba(255, 255, 255, 0.15);
  padding: 16px;
  border-radius: 10px;
  backdrop-filter: blur(5px);
}


/* Contact Section */
.contact-section p {
  margin-bottom: 0.75rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2rem;
  }

  .project-cards {
    flex-direction: column;
  }

  .card {
    flex: 1 1 100%;
  }
}
